import request from '@/utils/request'

export default {
  Login(data) {
    // 
    //{
    // username:''
    // password:''
    // }
    return request({
      method: 'post',
      url: '/login',
     data
    })
  },
  getReport(data) {
    // 
    // {
    //   "location": "",
    //   "beginYear": "2015",
    //   "endYear": "2022"
    // }
    return request({
      method: 'post',
      url: '/analyze',
      data,
    })
  },
  getTimeChina(data) {
    //  gdp
    // {
    //   "beginYear": "2010",
    //   "endYear": "2024"
    // }
    return request({
      method: 'post',
      url: '/queryData/chinaGdpList',
      data,
    })
  },
  getCity(data) {
    // gdp
    // {
    //   "province": "",
    //   "beginYear": "2015",
    //   "endYear": "2025"
    // }
    return request({
      method: 'post',
      url: '/queryData/provinceGdpList',
      data,
    })
  },
  getCountry(data){
    //gdp
    // {
    //   "country": "",
    //   "beginYear": "2010",
    //   "endYear": "2025"
    // }
    return request({
      method: 'post',
      url: '/queryData/countryGdpList',
      data,
    })
  },
  deChina(data){
    //GDP
    // {
    //   "beginYear": "2021",
    //   "endYear": "2022"
    // }
    return request({
      method: 'post',
      url: '/destroy/china',
      data,
    })
  },
  deCity(data){
    // GDP
    // {
    //   "province": "",
    //   "beginYear": "2021",
    //   "endYear": "2022"
    // }
    return request({
      method: 'post',
      url: '/destroy/china',
      data,
    })
  },


}